body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}

.App {
  width: 100%;
  height: 100%;
}

.ui.grid.Layout {
  height: 100%;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
}

.Layout-side {
  height: 100%;
  overflow-y: hidden;
  padding: 0 !important;
  margin: 0 !important;
}

.Layout-side-upper {
  width: 100% !important;
  margin: 0 !important;
  padding-left: 1rem;
  overflow-y: scroll;
  height: calc(100% - 80px);
  border-radius: 0 !important;
}

.Layout-main {
  height: 100%;
  overflow-y: hidden;
  padding: 0 !important;
}
.About {
  width: 100%;
  background: #41474d;
  color: #eee;
  padding: 8px;
  box-sizing: border-box;
  font-size: small;
  height: 80px;
}

.About a {
  color: #88c4ff;
}

.About p {
  margin-bottom: 0.5em;
}
.Playground {
  box-sizing: border-box;
  padding: 16px 32px;
  height: 100%;
  overflow-y: scroll;
}

.Playground-code {
  margin-top: 16px;
  margin-bottom: 16px;
}

.Playground-code-test {
  -webkit-transition: height 0.3s ease;
  transition: height 0.3s ease;
}

.Playground-code-hidden {
  height: 3px !important;
}

.Playground-result {
  padding: 12px;
  background: #fffccb;
  color: #444;
  font-weight: 600;
  border: 1px solid #666;
}

.ui.tag.label.Playground-solved {
  margin-left: 20px;
}

.Playground-test-code-header {
  margin-right: 0 !important;
}
.Playground-header-menu {
  padding-right: 0 !important;
}

.ui.modal.Share-modal {
  text-align: center;
}
.ui.modal.Share-modal>.actions {
  text-align: center;
}

.Share-actions {
  display: flex;
  justify-content: center;
  margin-bottom: 48px;
}
.Share-actions div {
  margin-left: 4px;
  margin-right: 4px;
  cursor: pointer;
}

.Share-close-button {
  padding: 8px;
  border: 1px solid #bbb;
  cursor: pointer;
}
